.formPrincipal {
  width: 100%;
  min-height: 100vh;
  background-image: url("../Img/actividad3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.divSection {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5rem;
}

.divSection > section {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  width: 20rem;
  height: fit-content;
  background-color: rgba(230, 230, 230, 0.7);
  border-radius: 0.5rem;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.divSection > section > label {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  height: 2rem;
  font-weight: bold;
}

.divSection > section > input {
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  width: 15rem;
  height: 1.5rem;
  font-weight: 600;
  border-radius: 0.4rem;
}

.divSection > section > select {
  width: 12rem;
  height: 1.5rem;
  text-transform: uppercase;
  font-weight: 550;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 0.5rem;
}

.ul{
    width: 12rem;
}

.ul > li {
    display: flex;
    flex-direction: row;
    gap: 0.2rem;
    text-transform: uppercase;
    align-items: center;  
}

.errors{
    color: red;
    font-size: 0.75rem;
    text-transform: uppercase;
}

.btnx{
    border-radius: 9999px;
    width: 1.5rem;
    height: 1.5rem;
    border: 0.1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn {
  width: 15rem;
  height: 3rem;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(230, 230, 230, 0.7);
  border-radius: 0.5rem;
  cursor: pointer;
}
